.arrow {
  width: 43px;
  height: 43px;
  margin-top: 40px;
  border: 2px solid #da353b;
  background: white;
  float: right;
  margin-right: 90px;
}
.arrow + .arrow {
  margin-right: 20px;
}
.arrow > div {
  box-sizing: border-box;
  display: inline-block;
}
.arrow > div:first-child {
  border-top: 1.5px solid #da353b;
  width: 15px;
  margin-bottom: 3.7px;
}
.arrow > div:nth-child(2) {
  width: 9px;
  height: 9px;
  border-top: 1.5px solid #da353b;
  border-right: 1.5px solid #da353b;
  border-bottom: 0;
  border-left: 0;
  transform: rotate(45deg);
  margin-left: -13.5px;
}
.arrow:hover {
  cursor: pointer;
  background: #da353b;
}
.arrow:hover > div:first-child {
  border-top: 1.5px solid white;
}
.arrow:hover > div:nth-child(2) {
  border-top: 1.5px solid white;
  border-right: 1.5px solid white;
}
.arrow-revers {
  transform: rotate(180deg);
}
@media (max-width: 1000px) {
  .arrow {
    display: none !important;
  }
}
